.fylo {
  margin-bottom: 4.375rem;
}
.fylo__illustration {
  width: 100%;
}
.fylo__header {
  margin-bottom: 2.8125rem;
  font-size: 2.1875rem;
}
@media (min-width: 40em) {
  .fylo__header {
    margin-bottom: 1.75rem;
    font-size: 1.875rem;
    text-align: left;
  }
}
.fylo__content {
  margin-bottom: 2.1875rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .fylo__content {
    text-align: left;
    font-size: 1rem;
  }
}
.fylo-form {
  gap: 0.9375rem;
}
@media (min-width: 40em) {
  .fylo-form {
    flex-direction: row;
    width: 90%;
  }
}
.fylo-form__label {
  display: none;
}
.fylo-form__email {
  padding: 0.625rem 1.25rem;
  width: 100%;
}
.fylo-form__submit {
  width: 100%;
  padding: 0.625rem 0;
  color: var(--white);
  background-color: var(--bright-blue);
  font-family: var(--font-headings);
  font-size: 1.125rem;
  font-weight: bold;
  border: none;
  border-radius: 0.3125rem;
  transition: opacity 200ms ease-in-out;
}
.fylo-form__submit:hover {
  cursor: pointer;
  opacity: 0.8;
}