@use '../util' as *;

.fylo {
  margin-bottom: rem(70);

  &__illustration {
    width: 100%;
  }

  &__header {
    margin-bottom: rem(45);
    font-size: rem(35);

    @include breakpoint(medium) {
      margin-bottom: rem(28);
      font-size: rem(30);
      text-align: left;
    }
  }

  &__content {
    margin-bottom: rem(35);
    line-height: 1.5;

    @include breakpoint(medium) {
      text-align: left;
      font-size: 1rem;
    }
  }

  &-form {
    gap: rem(15);

    @include breakpoint(medium) {
      flex-direction: row;
      width: 90%;
    }

    &__label {
      display: none;
    }

    &__email {
      padding: rem(10) rem(20);
      width: 100%;
    }

    &__submit {
      width: 100%;
      padding: rem(10) 0;
      color: var(--white);
      background-color: var(--bright-blue);
      font-family: var(--font-headings);
      font-size: rem(18);
      font-weight: bold;
      border: none;
      border-radius: rem(5);
      transition: opacity 200ms ease-in-out;

      &:hover {
        cursor: pointer;
        opacity: .8;
      }
    }
  }

}